export type LocaleType = "en-GB" | "tr-TR" | "en-US";
export interface IDateFormat {
    date?: {
        locale?: LocaleType;
    };
    time?: {
        second?: boolean; // default true
    };
}
export class DateHelper {
    public static formatDate(date: Date, format: IDateFormat) {
        if (typeof date == "string") { date = new Date(date); }
        let formatOpt: Intl.DateTimeFormatOptions = {};
        let locale: LocaleType = "tr-TR";
        if (format.date) {
            locale = format.date.locale || locale;
            formatOpt = { ...formatOpt, ...{ year: 'numeric', month: '2-digit', day: '2-digit' } };
        }
        if (format.time) {
            formatOpt.hour = formatOpt.minute = "2-digit";
            const second = format.time.second == false ? false : true;
            if (second) {
                formatOpt.second = "2-digit";
            }
        }

        const formatter = new Intl.DateTimeFormat(locale, formatOpt);
        return formatter.format(date);
    }
    public static toLocaleIsoString(date?: Date){
        date = date || new Date();
        return new Date(date.getTime() - (date.getTimezoneOffset() * 60000)).toISOString();
    }
}