___CSS_LOADER_EXPORT___.push([module.id, "\n.custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n}\n.custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n}\n.custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n}\n.select-display-type  i {\n        font-size: 16px;\n        color: #BCBCBC;\n}\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/backoffice/addOrUpdateAnnouncement.vue"],"names":[],"mappings":";AA0MI;QACI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;AAC9C;AACA;QACI,eAAe;QACf,kBAAkB;AACtB;AACA;QACI,yBAAyB;QACzB,cAAc;AAClB;AACA;QACI,eAAe;QACf,cAAc;AAClB","sourcesContent":["<script setup lang=\"ts\">\n    import { container } from \"tsyringe\";\n    import { computed, ref } from \"vue\";\n    import { IAnnouncementServiceObj } from \"../../../../../../../common/qCloudTemp/announcement\";\n    import { IBoAnnouncementUseCase } from \"../../../../../domain/useCase/backoffice/IBoAnnouncementUseCase\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import IDEInput from \"../../../components/application/IDEInput.vue\";\n    import CustomSelect from \"../../customSelect.vue\"\n    import CheckboxComp from \"../../checkboxComp.vue\";\n    import { DateHelper } from \"../../../../../../../common/helpers/dateHelper\";\n\n    interface IAddorUpdateAnnouncement {\n        announcement?: IAnnouncementServiceObj;\n    }\n\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const boAnnouncementUseCase = container.resolve<IBoAnnouncementUseCase>(IBoAnnouncementUseCase);\n    const props = defineProps<IAddorUpdateAnnouncement>();\n\n    const text = ref(props.announcement?.text);\n    const isActive = ref(props.announcement?.isActive || false);\n    const startDate = ref(strDateToCompDate(props.announcement?.startDate));\n    const endDate = ref(strDateToCompDate(props.announcement?.endDate));\n    const displayType = ref(props.announcement?.displayType);\n    const emit = defineEmits([\"close\"]);\n    const displayTypeOptions = [\"Header Bar\", \"Dialog\"];\n\n    function strDateToCompDate(value?: string) {\n        if (!value) { return undefined; }\n\n        const localeValue = DateHelper.toLocaleIsoString(new Date(value));\n        return localeValue.split(\".\")[0];\n    }\n\n    const isThereUpdate = () => {\n        if(props.announcement) {\n            return !(props.announcement!.text == text.value && props.announcement!.startDate == startDate.value && props.announcement!.endDate == endDate.value && props.announcement!.isActive == isActive.value && props.announcement!.displayType == displayType.value)\n        }\n        else {\n            return true;\n        }\n    }\n    function addOrUpdateAnnouncement() {\n        const newAnnouncementData = {text: text.value!, startDate: startDate.value, endDate: endDate.value, displayType: displayType.value!, isActive: isActive.value!};\n        const updateAnnouncementData = {id: props.announcement?.id!, ...newAnnouncementData};\n\n        executor.execute( async () => {\n            if(props.announcement) {\n                await boAnnouncementUseCase.updateAnnouncement(updateAnnouncementData);\n            }\n            else {\n                await boAnnouncementUseCase.createAnnouncement(newAnnouncementData);\n            }\n            close();\n        }, {loading: true})\n    }\n\n    function setTextValue(value: string) {\n        text.value = value\n    }\n\n    function close() {\n        emit(\"close\");\n    }\n    function setDisplayType(value: \"Header Bar\" | \"Dialog\"){\n        switch (value) {\n            case \"Header Bar\": displayType.value = \"headerBar\"; break;\n            case \"Dialog\": displayType.value = \"dialog\"; break;\n        }\n    }\n    function getDisplayTypeDefaultValue(){\n        switch (displayType.value) {\n            case \"headerBar\": return \"Header Bar\";\n            case \"dialog\": return \"Dialog\";\n            default: return undefined;\n        }\n    }\n    const addButtonDisabled = computed(() => !isThereUpdate() || !text.value || !startDate.value || !endDate.value || !displayType.value);\n\n</script>\n\n<template>\n    <div class=\"container\">\n        <div class='updateOrCreateTextAreaDiv'>\n            <IDEInput :isRequired=\"true\" @getValue=\"setTextValue\"  placeholder=\"\"\n                :type=\"'text'\" :value=\"props.announcement?.text\"  info-text=\"Announcement Text\" />\n        </div>\n        <div class=\"updateOrCreateTextAreaDiv\">\n            <label>Display Type</label>\n            <CustomSelect :options=\"displayTypeOptions\" class=\"select-display-type\" :placeholder=\"'Select'\" :default=\"getDisplayTypeDefaultValue()\" @input=\"setDisplayType\"></CustomSelect>\n        </div>\n        <div class='updateOrCreateInputDiv'>\n            <div>\n                <label for=\"start\">Start date:</label> \n                <input class=\"input-date\" type=\"datetime-local\" id=\"start\" name=\"trip-start\" v-model=\"startDate\" />\n            </div>\n            <div>\n                <label for=\"end\">End date:</label>\n                <input class=\"input-date\" style=\"margin-left: 16px;\" type=\"datetime-local\" id=\"end\" name=\"trip-end\" v-model=\"endDate\" :min=\"startDate?.toLocaleString()\" />\n            </div>\n        </div>\n        <div class='updateOrCreateInputDiv' style=\"display: flex\">\n            <label for=\"start\">Active</label>\n            <!-- <input type=\"checkbox\" v-model=\"isActive\"/> -->\n            <CheckboxComp style=\"margin-top: 2px; margin-left: 8px;\" :check=\"isActive\" @click=\"isActive = !isActive\" />\n        </div>\n        <div class='updateOrCreateRow'>\n            <div class=\"col\">\n                <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n                <button :style=\"{ color: addButtonDisabled ? '#757575' : '#FFFFFF' }\" class='updateOrCreateBtn'\n                    @click=\"addOrUpdateAnnouncement()\" :disabled=\"addButtonDisabled\">{{\n                            announcement ? \"SAVE\" : \"CREATE\"\n                    }}</button>\n            </div>\n        </div>\n    </div>\n\n</template>\n<style scoped>\n    * {\n        font-family: \"Roboto\";\n    }\n    .container {\n        padding: 32px 20px 20px\n    }\n    .updateOrCreateTextAreaDiv {\n        height: 77px;\n    }\n    .updateOrCreateTextarea {\n        width: 100%;\n        outline: none;\n        height: 120px;\n        border: 1px solid #A1A1A1;\n        border-radius: 4px;\n        opacity: 1;\n        resize: none;\n        margin-top: 3px;\n    }\n\n    .updateOrCreateInputDiv {\n        /* height: 173px; */\n\n    }\n    .input-date {\n        margin-left: 8px;\n        color: #212121;\n        outline: none;\n        height: 32px;\n        border: 1px solid #A1A1A1;\n        border-radius: 4px;\n        padding-left: 7px;\n        margin-bottom: 8px;\n    }\n\n    .updateOrCreateRow {\n        display: flex;\n        text-align: end;\n    }\n\n    .updateOrCreateBtn {\n        background-color: #151233;\n        color: #fff;\n        border-radius: 4px;\n        /* width: 72px; */\n        height: 40px;\n        font-weight: 500;\n        font-size: 16px;\n        padding: 8px 15px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    ::placeholder {\n        padding-left: 10px;\n        padding-top: 5px;\n        color: #A1A1A1;\n        font-style: normal;\n        font-family: \"ROBOTO\";\n        letter-spacing: 0px;\n        font-size: 14px;\n        opacity: 1px;\n    }\n    .select-display-type {\n        width: 100%;\n        font-size: 15px;\n        height: 40px;\n        line-height: 40px;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        padding: 0px 12px;\n    }\n</style>\n<style>\n\n    .custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    }\n    .custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n    .select-display-type  i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n\n</style>\n"],"sourceRoot":""}]);
